<template>
    <section class="content">
        <div class="container-fluid">
            <div class="row">

                <div class="col-12">

                    <div class="card" v-if="$gate.isAdmin()">
                        <div class="card-header">
                            <h3 class="card-title">Ticket List</h3>

                            <div class="card-tools">

                                <button type="button" class="btn btn-sm btn-primary" @click="newModal">
                                    <i class="fa fa-plus-square"></i>
                                    Add New
                                </button>
                            </div>
                        </div>
                        <!-- /.card-header -->
                        <div class="card-body table-responsive p-0">
                            <table class="table table-hover">
                                <thead>
                                    <tr>
                                        <th>Ticket No </th>
                                        <th>Machine Name</th>
                                        <th>Machine Far No</th>
                                        <th>Created Date</th>
                                        <th>Expected date</th>
                                        <th>Ticket Status</th>
                                        <th>Action</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="ticket in Tickets.data" :key="ticket.id">
                                        <td>{{ ticket.ticket_no }}</td>
                                        <td class="text-capitalize">{{ ticket.machine.name }}</td>
                                        <td>{{ ticket.machine.far_no }}</td>
                                        <td>{{ ticket.created_at | myDate }}</td>
                                        <td>{{ ticket.likely_date | myDate }}</td>
                                        <td v-html="ticket.status"></td>
                                        <td>
                                            <a href="javascript:void(0);" class="btn btn-success btn-sm"
                                                @click="editModal(ticket)">
                                                Edit
                                            </a> <a href="javascript:void(0);" class="btn btn-danger btn-sm"
                                                @click="closeModal(ticket)" title="Close ticket">
                                                Close
                                            </a>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <!-- /.card-body -->
                        <div class="card-footer">
                            <pagination :data="Tickets" @pagination-change-page="getResults"></pagination>
                        </div>
                    </div>
                    <!-- /.card -->
                </div>
            </div>


            <div v-if="!$gate.isAdmin()">
                <not-found></not-found>
            </div>


        </div>
        <!-- Modal -->
        <div class="modal fade" id="addNew" tabindex="-1" role="dialog" aria-labelledby="addNew" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" v-show="!editmode">Create New Ticket</h5>
                        <h5 class="modal-title" v-show="editmode">Edit Ticket</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>

                    <form @submit.prevent="submitTicket()">
                        <div class="modal-body">
                            <div class="row">


                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Machine Site</label>
                                        <v-select label="site_name" :reduce="(option) => option.id" :options="sites"
                                            placeholder="Choose Site ..." v-model="form.site_id" v-validate="'required'"
                                            :class="{ 'is-invalid': verrors.has('site_id') }" data-vv-as="site"
                                            class="required" name="site_id" :disabled="form.id!=''">
                                        </v-select>
                                        <div v-if="verrors.has('site_id')" class="help-block invalid-feedback">
                                            {{ verrors.first('site_id') }}
                                        </div>
                                    </div>
                                </div>

                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Machine Category</label>
                                        <v-select label="name" :reduce="(option) => option.id" :options="categories"
                                            placeholder="Choose Category ..." v-model="form.category_id"
                                            v-validate="'required'"
                                            :class="{ 'is-invalid': verrors.has('category_id') }" data-vv-as="Category"
                                            class="required" name="category_id" :disabled="form.id!=''">
                                        </v-select>
                                        <div v-if="verrors.has('category_id')" class="help-block invalid-feedback">
                                            {{ verrors.first('category_id') }}
                                        </div>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Machine Description</label>
                                        <v-select label="name" :reduce="(option) => option.id" :options="sub_categories"
                                            placeholder="Choose description ..." v-model="form.sub_category_id"
                                            v-validate="'required'"
                                            :class="{ 'is-invalid': verrors.has('sub_category_id') }"
                                            data-vv-as="description" class="required" name="sub_category_id"
                                            :disabled="form.id!=''">
                                        </v-select>
                                        <div v-if="verrors.has('sub_category_id')" class="help-block invalid-feedback">
                                            {{ verrors.first('sub_category_id') }}
                                        </div>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Select Machine</label>
                                        <v-select label="name" :get-option-label="getLabel"
                                            :reduce="(option) => option.id" :options="machines"
                                            placeholder="Enter machines ..." v-model="form.machine_id"
                                            v-validate="'required'" :class="{ 'is-invalid': verrors.has('machine_id') }"
                                            name="machine_id" data-vv-as="Select machine" :disabled="form.id!=''">
                                        </v-select>
                                        <div v-if="verrors.has('machine_id')" class="help-block invalid-feedback">{{
                                        verrors.first('machine_id')
                                        }}</div>
                                    </div>
                                </div>

                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Maintenance Provider</label>
                                        <select v-model="form.maintenance_provider" v-validate="'required'"
                                            :class="{ 'is-invalid': verrors.has('maintenance_provider') }"
                                            name="maintenance_provider" data-vv-as="Maintenance Provider"
                                            class="form-control">
                                            <option value="vendor">Vendor</option>
                                            <option value="user">Internal User</option>
                                        </select>
                                        <div v-if="verrors.has('maintenance_provider')"
                                            class="help-block invalid-feedback">{{
                                            verrors.first('maintenance_provider')
                                            }}</div>
                                    </div>
                                </div>

                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Select {{ form.maintenance_provider == 'user' ? 'user' : 'vendor' }}
                                        </label>
                                        <template v-if="form.maintenance_provider=='vendor'">
                                        <v-select label="name" :reduce="(option) => option.id" :options="users"
                                            placeholder="Enter users ..." v-model="form.vendor_id"
                                            v-validate="'required'" :class="{ 'is-invalid': verrors.has('vendor_id') }"
                                            name="vendor_id" data-vv-as="Select vendor">
                                        </v-select>
                                        <div v-if="verrors.has('vendor_id')" class="help-block invalid-feedback">{{
                                        verrors.first('vendor_id')
                                        }}</div>
</template>
<template v-else>

<v-select label="name" :reduce="(option) => option.id" :options="users"
                                            placeholder="Enter users ..." v-model="form.user_id"
                                            v-validate="'required'" :class="{ 'is-invalid': verrors.has('user_id') }"
                                            name="user_id" data-vv-as="Select user">
                                        </v-select>
                                        <div v-if="verrors.has('user_id')" class="help-block invalid-feedback">{{
                                        verrors.first('user_id')
                                        }}</div>

</template>

                                    </div>
                                </div>

                                <div class="col-6" v-if="form.maintenance_provider=='vendor'">
                                    <div class="form-group">
                                        <label>Service Type</label>
                                        <div class="custom-control custom-radio">
                                            <input type="radio" id="customRadio1" name="customRadio"
                                                class="custom-control-input" value="Paid Service"
                                                v-model="form.service_type" :disabled="form.id!=''">
                                            <label class="custom-control-label" for="customRadio1">Paid Service</label>
                                        </div>
                                        <div class="custom-control custom-radio">
                                            <input type="radio" id="customRadio2" name="customRadio"
                                                class="custom-control-input" value="Free Service"
                                                v-model="form.service_type"
                                                :disabled="(selected_machine && (selected_machine.completed_free_service >= selected_machine.free_service_no))||form.id!=''">
                                            <label class="custom-control-label" for="customRadio2">Free Service</label>
                                        </div>
                                    </div>
                                </div>

                                <div class="col-6" v-if="form.service_type == 'Free Service'">
                                    <div class="form-group">
                                        <label>Free service no</label>
                                        <div class="input-group">
                                            <input v-model="form.free_service_no" type="text" name="free_service_no"
                                                v-validate="'required'" data-vv-as="free service no"
                                                class="form-control"
                                                :class="{ 'is-invalid': verrors.has('free_service_no') }" readonly
                                                aria-describedby="free_service_no">
                                            <div class="input-group-append">
                                                <span class="input-group-text" id="free_service_no"> /
                                                    {{selected_machine?selected_machine.free_service_no:''}}</span>
                                            </div>
                                        </div>
                                        <div v-if="verrors.has('free_service_no')" class="help-block invalid-feedback">
                                            {{
                                            verrors.first('free_service_no')
                                            }}</div>
                                    </div>
                                </div>

                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Ticket Type</label>
                                        <select v-model="form.ticket_type" name="ticket_type" v-validate="'required'"
                                            data-vv-as="Ticket Type" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('ticket_type') }"
                                            :disabled="form.id!=''">
                                            <option value="Periodic Maintenance">Periodic Maintenance</option>
                                            <option value="Breakdown Maintenance">Breakdown Maintenance</option>
                                        </select>
                                        <div v-if="verrors.has('ticket_type')" class="help-block invalid-feedback">
                                            {{ verrors.first('ticket_type') }}
                                        </div>
                                    </div>
                                </div>
                                <!--  <div class="col-6">
                                    <div class="form-group">
                                        <label>Ticket No </label>
                                        <input v-model="form.ticket_no" type="text" name="ticket_no"
                                            v-validate="'required'" data-vv-as="Ticket No" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('ticket_no') }" readonly>
                                        <div v-if="verrors.has('ticket_no')" class="help-block invalid-feedback"> {{
                                                verrors.first('ticket_no')
                                        }}</div>
                                    </div>
                                </div> -->
 
                                
                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Estimated Material cost</label>
                                        <input v-model="form.estimated_material_cost" type="text"
                                            name="estimated_material_cost" v-validate="'required'"
                                            data-vv-as="Estimated Material cost" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('estimated_material_cost') }"
                                            :disabled="form.id!=''">
                                        <div v-if="verrors.has('estimated_material_cost')"
                                            class="help-block invalid-feedback"> {{
                                            verrors.first('estimated_material_cost')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="form-group">
                                        <label> Estimated Material cost PDF</label>
                                        <input type="file" @change="uploadMaterialCost" multiple name="material_cost"
                                            ref="filematerial" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('material_cost') }" v-validate="''"
                                            data-vv-as="Estimated Material cost" />
                                        <div v-if="verrors.has('material_cost')" class="help-block invalid-feedback"> {{
                                        verrors.first('material_cost')
                                        }}</div>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Estimated Service cost</label>
                                        <input v-model="form.estimated_service_cost" type="text"
                                            name="estimated_service_cost" v-validate="'required'"
                                            data-vv-as="Estimated Service cost" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('estimated_service_cost') }"
                                            :disabled="form.id!=''">
                                        <div v-if="verrors.has('estimated_service_cost')"
                                            class="help-block invalid-feedback"> {{
                                            verrors.first('estimated_service_cost')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="form-group">
                                        <label> Estimated Service cost </label>

                                        <input type="file" @change="uploadServiceCost" multiple name="service_cost"
                                            ref="fileservice" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('service_cost') }" v-validate="''"
                                            data-vv-as="Estimated Service cost" />
                                        <div v-if="verrors.has('service_cost')" class="help-block invalid-feedback"> {{
                                        verrors.first('service_cost')
                                        }}</div>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Expected date of Maintenance</label>
                                        <datetime v-model="form.likely_date" name="likely_date"
                                            input-class="form-control"
                                            :class="{ 'is-invalid': verrors.has('likely_date') }"
                                            v-validate="'required'" data-vv-as="Expected date"
                                            :disabled="form.id!=''" />
                                        <div v-if="verrors.has('likely_date')" class="help-block invalid-feedback">{{
                                        verrors.first('likely_date')
                                        }}</div>
                                    </div>
                                </div>

                                <div class="col-12" v-if="form.id">
                                    <h6><strong>Stage</strong></h6>
                                    <template v-for="(field, x) in stages">
                                        <div class="row ">
                                            <div class="col-3 ">
                                                <select v-model="field.stage_no" class="form-control"
                                                    v-validate="'required'" name="stage_no"
                                                    :class="{ 'is-invalid': verrors.has('stage_no') }">
                                                    <option value="" disabled>Select stage</option>
                                                    <option :value="stg" v-for="(stg,index) in opn_stage" :key="index">
                                                        {{ stg }}</option>
                                                </select>
                                            </div>
                                            <div class="col-2">
                                                <datetime v-model="field.stage_date" name="stage_date"
                                                    input-class="form-control" placeholder="Select date"
                                                    :class="{ 'is-invalid': verrors.has('stage_date') }"
                                                    v-validate="''" data-vv-as="Date" :key="'stgdate'+x" :max-datetime="new Date().toISOString()"/>
                                            </div>
                                            <div class="col-4">
                                                <input v-model="field.description" class="form-control"
                                                    placeholder="Enter Description">
                                            </div>
                                            <div class="col-3">
                                                <input type="file" :name="'cfile' + x" class="form-control"
                                                    placeholder="choose file" @change="choose_stage_file($event, x)">
                                                <br>
                                                <a :href="set_image(field.file)" target="_blank" v-if="field.file">View
                                                    file</a>
                                            </div>
                                        </div>
                                    </template>
                                    <button @click="AddStage" type="button" class="btn btn-success float-right">Add
                                        +</button>
                                </div>
                            </div>
                        </div>

                        <div class="modal-footer">
                            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                            <button v-show="editmode" type="submit" class="btn btn-success">Update</button>
                            <button v-show="!editmode" type="submit" class="btn btn-primary">Create</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>


        <div class="modal fade" id="closemodal" tabindex="-1" role="dialog" aria-labelledby="closemodal"
            aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">Close Ticket</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>

                    <form @submit.prevent="submitCloseTicket()" data-vv-scope="closeticket">
                        <div class="modal-body">
                            <div class="row">

                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Actual Date</label>
                                        <datetime v-model="formclose.actual_date" name="actual_date"
                                            input-class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.actual_date') }"
                                            v-validate="'required'" data-vv-as="Actual Date" />
                                        <div v-if="verrors.has('closeticket.actual_date')"
                                            class="help-block invalid-feedback">{{
                                            verrors.first('closeticket.actual_date')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Machine Status</label>
                                        <select v-model="formclose.machine_status" name="machine_status"
                                            class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.machine_status') }"
                                            v-validate="'required'" data-vv-as="machine status">

                                            <option value="Break Down">Break Down</option>
                                 <option value="Running">Running</option>
                                 <option value="Running with Problem">Running with Problem</option>
                                 <option value="Idle">Idle</option>
                                            </select>                                            
                                        <div v-if="verrors.has('closeticket.machine_status')"
                                            class="help-block invalid-feedback">{{
                                            verrors.first('closeticket.machine_status')
                                            }}</div>
                                    </div>
                                </div>
                                
                                <div class="col-4">
                                    <div class="form-group">
                                        <label>Estimated Material cost</label>
                                        <input v-model="pop_ticket.estimated_material_cost" type="text"
                                            class="form-control" readonly>
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="form-group">
                                        <label>Actual Material cost</label>
                                        <input v-model="formclose.actual_material_cost" type="text"
                                            name="actual_material_cost" v-validate="'required'"
                                            data-vv-as="Actual Material cost" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.actual_material_cost') }">
                                        <div v-if="verrors.has('closeticket.actual_material_cost')"
                                            class="help-block invalid-feedback"> {{
                                            verrors.first('closeticket.actual_material_cost')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="form-group">
                                        <label> Actual Material cost PDF</label>
                                        <input type="file" @change="uploadMaterialCost" multiple name="material_cost"
                                            ref="fileactualmaterial" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.material_cost') }"
                                            v-validate="''" data-vv-as="Actual Material cost" />

                                        <div v-if="verrors.has('closeticket.material_cost')"
                                            class="help-block invalid-feedback"> {{
                                            verrors.first('closeticket.material_cost')
                                            }}</div>
                                    </div>
                                </div>

                                <div class="col-4">
                                    <div class="form-group">
                                        <label>Estimated Service cost</label>
                                        <input v-model="pop_ticket.estimated_service_cost" type="text"
                                            class="form-control" readonly>
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="form-group">
                                        <label>Actual Service cost</label>
                                        <input v-model="formclose.actual_service_cost" type="text"
                                            name="actual_service_cost" v-validate="'required'"
                                            data-vv-as="Actual Service cost" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.actual_service_cost') }">
                                        <div v-if="verrors.has('closeticket.actual_service_cost')"
                                            class="help-block invalid-feedback"> {{
                                            verrors.first('closeticket.actual_service_cost')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="form-group">
                                        <label> Actual Service cost PDF </label>
                                        <input type="file" @change="uploadServiceCost" multiple name="service_cost"
                                            ref="fileactualservice" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.service_cost') }"
                                            v-validate="''" data-vv-as="Actual Service cost" />
                                        <div v-if="verrors.has('closeticket.service_cost')"
                                            class="help-block invalid-feedback"> {{
                                            verrors.first('closeticket.service_cost')
                                            }}</div>
                                    </div>
                                </div>

                                <div class="col-6" v-if="pop_machine.is_consumtion_hr_run">
                                    <div class="form-group">
                                        <label>Current Run hour </label>
                                        <input v-model="formclose.current_run_hour" name="current_run_hour"
                                            class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.current_run_hour') }"
                                            v-validate="'required'" data-vv-as="Current Run hour" />
                                        <div v-if="verrors.has('closeticket.current_run_hour')"
                                            class="help-block invalid-feedback">
                                            {{
                                            verrors.first('closeticket.current_run_hour')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-6" v-if="pop_machine.is_consumtion_km_run">
                                    <div class="form-group">
                                        <label> Current KM Run </label>
                                        <input v-model="formclose.current_km_run" name="current_km_run"
                                            class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.current_km_run') }"
                                            v-validate="'required'" data-vv-as="Current KM hour" />
                                        <div v-if="verrors.has('closeticket.current_km_run')"
                                            class="help-block invalid-feedback">{{
                                            verrors.first('closeticket.current_km_run')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-6" v-if="pop_machine.is_production_fig">
                                    <div class="form-group">
                                        <label> Current Production fig </label>
                                        <input v-model="formclose.current_production_fig" name="current_production_fig"
                                            class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.current_production_fig') }"
                                            v-validate="'required'" data-vv-as="Current Production fig" />
                                        <div v-if="verrors.has('closeticket.current_production_fig')"
                                            class="help-block invalid-feedback">{{
                                            verrors.first('closeticket.current_production_fig')
                                            }}</div>
                                    </div>
                                </div>

                                <div class="col-6" v-if="formclose.ticket_type == 'Breakdown Maintenance'">
                                    <div class="form-group">
                                        <label>Production Loss</label>
                                        <input v-model="formclose.production_loss" name="production_loss"
                                            class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.production_loss') }"
                                            v-validate="'required'" data-vv-as="Production Loss" />
                                        <div v-if="verrors.has('closeticket.production_loss')"
                                            class="help-block invalid-feedback">
                                            {{
                                            verrors.first('closeticket.production_loss')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-6" v-if="formclose.ticket_type == 'Breakdown Maintenance'">
                                    <div class="form-group">
                                        <label>Production loss amount</label>
                                        <input v-model="formclose.production_loss_amount" name="production_loss_amount"
                                            class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.production_loss_amount') }"
                                            v-validate="'required'" data-vv-as="Current Production fig" />
                                        <div v-if="verrors.has('closeticket.production_loss_amount')"
                                            class="help-block invalid-feedback">{{
                                            verrors.first('closeticket.production_loss_amount')
                                            }}</div>
                                    </div>
                                </div>


                            </div>
                        </div>

                        <div class="modal-footer">
                            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                            <button type="submit" class="btn btn-primary">Close Ticket</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import VueTagsInput from '@johmun/vue-tags-input';
export default {
    components: {
        VueTagsInput,
    },
    data() {
        return {
            categories: [],
            sub_categories: [],
            sites: [],
            selected_machine: '',
            users: [],
            machines: [],
            Tickets: {},
            editmode: false,
            MaterialCost: null,
            ServiceCost: null,
            form: new Form({
                id: '',
                site_id: '',
                category_id: '',
                sub_category_id: '',
                free_service_no: '',
                ticket_type: 'Periodic Maintenance',
                service_type: 'Paid Service',
                machine_id: '',
                ticket_no: '',
                vendor_id: '',
                user_id:'',
                likely_date: '',
                estimated_material_cost: '',
                estimated_service_cost: '',
                maintenance_provider: 'vendor',
            }),
            stages: [{ id: '', stage_no: '', description: '', file: '', new_file: '' }],

            formclose: new Form({
                id: '',
                machine_status:'',
                edit_type: 'closeticket',
                ticket_type: 'Periodic Maintenance',
                actual_date: '',
                actual_material_cost: '',
                actual_service_cost: '',
                current_run_hour: '',
                current_km_run: '',
                current_production_fig: '',
                production_loss: '',
                production_loss_amount: '',
                status: 'Closed',
                vendor_resolve_date: '',
                vendor_attain_date: '',
                vendor_response_date: '',
                vendor_call_date: '',
            }),
            pop_machine: '',
            pop_ticket: '',
            opn_stage: ['Vendor created', 'Vendor Assigned', 'Order issued', 'Payment under process', 'Payment Done', 'Logistics under process', 'Materials despatched', 'Material received at site', 'Maintainace under progress'],
        }
    },
    methods: {
        getLabel(val) {
            if (typeof val === 'object') {
                return val.far_no + ' - ' + val.name;
            } else {
                return val;
            }
        },
        AddStage() {
            this.stages.push({ id: '', stage_no: '',stage_date:'', description: '', file: '', new_file: '' });
        },
        async choose_stage_file(event, x) {
            this.stages[x].new_file = event.target.files[0];
        },

        async uploadMaterialCost(event) {
            this.MaterialCost = event.target.files
        },
        async uploadServiceCost(event) {
            this.ServiceCost = event.target.files
        },

        getResults(page = 1) {
            this.$Progress.start();
            axios.get('/api/ticket?page=' + page).then(({ data }) => (this.Tickets = data.data));
            this.$Progress.finish();
        },
        submitCloseTicket() {
            this.$validator.validateAll('closeticket').then(async (valid) => {
                if (valid) {
                    let pop_loader = this.$loading.show({
                        container: this.$refs.vps_submit_pop,
                    });

                    this.formclose.put('/api/ticket/' + this.formclose.id)
                        .then((response) => {
                            if (response.data.success) {
                                $('#closemodal').modal('hide');
                                this.$validator.reset();
                                this.formclose.reset();
                                Toast.fire({
                                    icon: 'success',
                                    title: response.data.message
                                });
                                this.upload_files(this.formclose.id, 'actual')
                                this.loadTickets();
                            }
                            pop_loader.hide()
                        })
                        .catch(() => {
                            pop_loader.hide()
                            if (typeof err.response.data != 'undefined') {
                                this.$setErrorsFromResponse(err.response.data);
                            }
                        })
                }
            });
        },

        closeModal(ticket) {
            $('#closemodal').modal('show');
            this.$validator.reset();
            this.formclose.reset();
            this.$refs.fileactualservice.value = null;
            this.$refs.fileactualmaterial.value = null;
            this.pop_ticket = ticket
            this.pop_machine = ticket.machine
            this.formclose.fill(ticket);
            this.formclose.edit_type = 'closeticket'
        },
        editModal(ticket) {
            this.editmode = true;
            this.$validator.pause();
            this.form.reset();
            this.$refs.fileservice.value = null;
            this.$refs.filematerial.value = null;
            this.MaterialCost = null
            this.ServiceCost = null
            $('#addNew').modal('show');
            this.form.fill(ticket);
            this.stages = []
            if (ticket.stages) {
                this.stages = ticket.stages
            }
            this.$validator.resume();
        },
        newModal() {
            this.stages = []
            this.AddStage()
            this.editmode = false;
            this.$validator.reset();
            this.$validator.pause();
            this.form.reset();
            this.$refs.fileservice.value = null;
            this.$refs.filematerial.value = null;
            this.MaterialCost = null
            this.ServiceCost = null
            $('#addNew').modal('show');
            this.$validator.resume();
        },

        loadTickets() {
            if (this.$gate.isAdmin()) {
                axios.get("/api/ticket").then(({ data }) => (this.Tickets = data.data));
            }
        },

        async upload_files(id, isactual = false) {
            let fl = 0
            if (this.MaterialCost) {
                this.MaterialCost.forEach(file => {
                    var fd = new FormData();
                    fd.append(`file`, file);
                    if (isactual) {
                        fd.append(`file_type`, 'Actual Estimated Material');
                    } else {
                        fd.append(`file_type`, 'Estimated Material');
                    }
                    fd.append(`ticket_id`, id);
                    axios.post('api/ticket/files', fd).then((res) => {
                        console.log('gallery uploaded')
                    })
                });
            }
            if (this.ServiceCost) {
                this.ServiceCost.forEach(file => {
                    var fd = new FormData();
                    fd.append(`file`, file);
                    if (isactual) {
                        fd.append(`file_type`, 'Actual Estimated Service');
                    } else {
                        fd.append(`file_type`, 'Estimated Service');
                    }
                    fd.append(`ticket_id`, id);
                    axios.post('api/ticket/files', fd).then((res) => {
                        console.log('gallery uploaded')
                    })
                });
            }
        },

        async upload_stages(id) {
            if (this.stages) {
                this.stages.forEach(stage => {
                    var fd = new FormData();
                    fd.append(`id`, stage.id);
                    fd.append(`stage_no`, stage.stage_no);
                    fd.append(`stage_date`, stage.stage_date);
                    fd.append(`description`, stage.description);
                    fd.append(`new_file`, stage.new_file);
                    fd.append(`file`, stage.file);
                    fd.append(`ticket_id`, id);
                    axios.post('api/ticket/stages', fd).then((res) => {
                        console.log('stages uploaded')
                    })
                });
            }
        },

        submitTicket() {
            this.$validator.validateAll().then(async (valid) => {
                if (valid) {
                    let pop_loader = this.$loading.show({
                        container: this.$refs.vps_submit_pop,
                    });
                    if (this.form.id) {
                        this.form.put('/api/ticket/' + this.form.id)
                            .then((response) => {
                                if (response.data.success) {
                                    $('#addNew').modal('hide');
                                    Toast.fire({
                                        icon: 'success',
                                        title: response.data.message
                                    });
                                    this.upload_stages(this.form.id)
                                    this.upload_files(this.form.id)
                                    this.loadTickets();
                                }
                                pop_loader.hide()
                            })
                            .catch(() => {
                                pop_loader.hide()
                                if (typeof err.response.data != 'undefined') {
                                    this.$setErrorsFromResponse(err.response.data);
                                }
                            })

                    } else {
                        this.form.post('/api/ticket')
                            .then((response) => {
                                if (response.data.success) {
                                    $('#addNew').modal('hide');
                                    Toast.fire({
                                        icon: 'success',
                                        title: response.data.message
                                    });

                                    this.upload_files(response.data.data.id)
                                    this.loadTickets();
                                }
                                pop_loader.hide()
                            })
                            .catch(() => {
                                pop_loader.hide()
                                if (typeof err.response.data != 'undefined') {
                                    this.$setErrorsFromResponse(err.response.data);
                                }
                            })
                    }

                }
            });


        },

        async check_free_service() {
            if (this.selected_machine) {
                if (this.selected_machine.completed_free_service >= this.selected_machine.free_service_no) {
                    this.form.service_type = 'Paid Service';
                    this.form.free_service_no = ''
                } else {
                    this.form.free_service_no = (this.selected_machine.completed_free_service + 1)
                }


                if (this.selected_machine && this.selected_machine.machine_type == 'New' && this.form.service_type == 'Free Service') {
                    //  this.selected_machine.free_service_no
                }
            }
        },

        async get_machine() {
            axios.get("/api/ticket_machine", {
                params: {
                    site_id: this.form.site_id,
                    category_id: this.form.category_id,
                    sub_category_id: this.form.sub_category_id,
                }
            }).then(response => {
                this.machines = response.data.data;
            }).catch(() => console.warn('Oh. Something went wrong'));
        }

    },
    mounted() {

    },
    created() {
        this.loadTickets();
    },
    beforeCreate() {
        axios.get("api/get_pre_machine")
            .then((res) => {
                this.categories = res.data.data.Category;
                this.sites = res.data.data.sites;
            })
        axios.get("/api/initial_ticket").then(response => {
            this.users = response.data.data.vendors;
            this.machines = response.data.data.machines;
        }).catch(() => console.warn('Oh. Something went wrong'));
    },
    watch: {
        async 'form.sub_category_id'(n, o) {
            if (n) {
                this.get_machine()
            }
        },
        async 'form.site_id'(n, o) {
            if (n) {
                this.get_machine()
            }
        },

        'form.category_id': {
            handler: function (n, o) {

                if (n) {
                    axios.get("api/category/" + n)
                        .then((res) => {
                            this.sub_categories = res.data.data;
                        })
                    this.get_machine()
                }
            },
            deep: true,
            initial: true
        },


        async 'form.maintenance_provider'(n) {
            if (n != 'vendor') {
                this.form.service_type = 'Paid Service'
            } 
            axios.get("api/user/list", {
                params: {
                    type: n
                }
            })
                .then(response => {
                    this.users = response.data.data;
                }).catch(() => console.warn('Oh. Something went wrong'));
        },

        async 'form.machine_id'(n) {
            this.selected_machine = this.machines.find(x => x.id === n);
            await this.check_free_service()
        },
        async 'form.service_type'(n) {
            if (n == 'Free Service') {
                await this.check_free_service()
            }
        }

    },
}
</script>
